@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");


body {
  margin: 0;
  padding: 0;
  background-color: white;
  text-align: center;
  font-family: "Poppins";
  font-size: 16px;
}

.container {
  
  max-width: 1440px;
  max-height: 60%;
  background-image: url("./images/bg-intro-desktop.png");
  background-color: hsl(0, 100%, 74%);
  display: inline-grid;
  grid-template-columns: 1fr 1fr;
  padding: 3%;
  margin: 5%;
}


.left {
  width: 80%;
  text-align: left;
  margin: auto;
}

.right {
  width: 80%;
  text-align: center;
  margin: auto;
}

.topleft {
  color: white;
  font-size: 32px;
  font-weight: 700;
  line-height: 1;
}

.bottomleft {
  color: white; 
}

.topright {
  color: white;
  background-color: hsl(248, 32%, 49%);
  font-weight: 700;
  border-radius: 15px;
  padding: 1%;
  margin-bottom: 5%;
  box-shadow: 0px 5px 10px hsl(249, 10%, 26%);
}

.price {
font-size: 13px;
font-weight: 400;
}

.bottomright {
  box-shadow: 0px 5px 10px hsl(249, 10%, 26%);
  margin-bottom: 5%;
}

form {
  text-align: center;
  background-color: white;
  border-radius: 10px;
}

input {
  color: hsl(249, 10%, 26%);
  font-family: "Poppins";
  font-weight: 600;
  width: 80%;
}

input[type=text], input[type=email] {
    width: 80%;
    padding: 15px 20px;
    margin: 1px 0;
    border-radius: 10px;
    margin-bottom: 20px;
}

input[type=submit] {
  width: 88%;
  padding: 15px 20px;
  margin: 1px 0;
  border-radius: 10px;
  margin-bottom: 5px;
  color: white;
  background-color: hsl(154, 59%, 51%);
  box-shadow: 0px 5px 10px hsl(249, 10%, 26%);
}

#firstname {
  margin-top: 5%;
}
#fine-print {
  padding-bottom: 10%;
}

.agree, .terms{
  font-size: 11px;
}

.terms {
  color: red;
  font-weight: 700;
}


@media (max-width: 375px) {

  body {
    background-image: url("./images/bg-intro-mobile.png");
    background-color: hsl(0, 100%, 74%);
  }
  .container {
      background-image: url("./images/bg-intro-mobile.png");
      display: block;
      text-align: center;
      margin: 0;
      height: 100%;
  }

  .left {
      width: 100%;
      float: none;
      text-align: center;
  }

  .right {
      width: 100%;
      float: none;
      text-align: center;
  }
}

